import api from "../config/api.js"
import Util from "../utils/utils"
import loading from "./loading.vue"
import countdown from "./countdown.vue"
import  Velocity from 'velocity-animate'

export default {
  components: {
    loading,
    countdown
  },
  data() {
    return {
      token: "",
      currentRoomId: "",
      uploadText: "",
      points: 0,
      totalPoints: 0,
      city: "",
      area: "",
      status: "",
      name: "",
      num: "",
      id: "",
      clickAble: true,
      loading: true,
      tip: false,
      // showBubble: true,
      countDownTime: 600,
      userInfo1:[{
        id: 1,
        text: 10000
      },
      {
        id: 2,
        text: 20000
      },
      {
        id: 3,
        text: 30000
      },
      {
        id: 4,
        text: 40000
      },
      {
        id: 5,
        text: 50000
      },
      {
        id: 6,
        text: 60000
      }
      ],
      userInfo2:[{
        id: 1,
        text: 11111
      },
      {
        id: 2,
        text: 21111
      },
      {
        id: 3,
        text: 31111
      },
      {
        id: 4,
        text: 41111
      },
      {
        id: 5,
        text: 51111
      },
      {
        id: 6,
        text: 61111
      }
      ]
    }
  },
  created() {
    this.init()
  },
  computed: {
    showFirst () {
      if (this.userInfo1 && this.userInfo1.length <= 6) {
        return true
      }
    },
    showSecond () {
      if (this.showFirst) {
        return false
      }
    }
  },
  // watch: {
  //   showFirst(newVal) {
  //     if (this.userInfo1 && this.userInfo1.length <= 6) {
  //       return
  //     }
  //   }
  // },
  mounted() {
    // if (this.currentRoomId === 0) {
    //   this.$toast('您还不是奥园业主，请先认证房屋。', 1500)
    // } else {
    //   this.getParam()
    // }
  },
  methods: {
    init() {
      this.getToken()
      this.getRoomId()
    },
    getToken() {
      this.token = Util.pageUrlGetValue("token")
      Util.setCookie("token", this.token, "aylives.cn")
    },
    getRoomId() {
      this.currentRoomId = Util.pageUrlGetValue("currentRoomId")
      Util.setCookie("currentRoomId", this.currentRoomId, "aylives.cn")
    },
    getParam() {
      api.Axios.get(api.USERINFO).then(res => {
        if (res.data.code === 200) {
          this.area = res.data.data.agencyName
          this.city = res.data.data.cityName
          this.status = res.data.data.added
          if (this.status) {
            this.$router.push({ path: "/poster" })
          }
        } else {
          this.$toast(res.data.msg, 1500)
        }
      })
    },
    collect (item) {
      let index = item.id
      let oId = document.getElementById(`id${index}`)
      if (this.showFirst) {
        if (this.userInfo1.length > 1) {
          this.userInfo1.length--
          console.log("------", this.userInfo1.length);
        } else {
          this.showFirst = false
          this.showSecond = true
        }
      }

      if (this.showSecond) {
        this.userInfo2.length--
        // if (this.userInfo2.length === 0) {
        //   this.showSecond = false
        // }
      }

      Velocity(oId,
        {
          top: 40,
          opacity: 0
        }, {
        duration: 1000
      })
    },
    jumpToRank () {
      this.$router.push({ path: "/rank"})
    }
  }
};



